import "./wish.css";
import bg from "../../images/bg.png";
import lily from "../../images/lily.png";
import bb from "../../images/bb.png";
import burg from "../../images/burger.png";
import dort from "../../images/doritos.png";
import fro from "../../images/fraro.png";
import lays from "../../images/lays.png";
import ras from "../../images/ras.png";
import nu from "../../images/Nukkad.jpg";
import off from "../../images/office.jpg";
import pz from "../../images/pizza.jpg";
import rk from "../../images/rockstar.jpg";
import friends from "../../images/friends.jpg";
import { Fade, Zoom } from "react-reveal";

export default function Wish() {
  return (
    <div className="w-cont">
      <img className="bg" src={bg} alt="backGround" />
      <Fade top>
        <div className="mt20">
          And here is some random stuff that I found nearby...
        </div>
      </Fade>
      <Zoom right cascade>
        <img className="gift" src={lily} alt="white lily" />
        <img className="gift" src={bb} alt="white lily" />
        <img className="gift" src={rk} alt="white lily" />
        <img className="gift" src={burg} alt="white lily" />
        <img className="gift" src={lays} alt="white lily" />
        <img className="gift" src={ras} alt="white lily" />
        <img className="gift" src={friends} alt="white lily" />
        <img className="gift" src={pz} alt="white lily" />
        <img className="gift" src={nu} alt="white lily" />
        <img className="gift" src={dort} alt="white lily" />
        <img className="gift" src={fro} alt="white lily" />
        <img className="gift" src={off} alt="white lily" />
      </Zoom>
    </div>
  );
}
